// prices-3
.prices {

    &__top {
        &_text {
            flex-basis: 50%;
        }
    }

    &__title {
        margin-bottom: 20px;
    }

    &__sort {
        display: flex;
        gap: 10px;
        margin-bottom: 40px;
        > * {
            flex: 1 0 30%;
        }
    }

    &__search {
        height: 100%;
        & input {
            height: 100%;
        }
    }

    &__table {
        display: block;
        width: 100%;

        &_body {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 12px;
        }

        &_item {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 40px;
            border-radius: 20px;
            padding: 30px;
            background: var(--fon-plashek-bezhevyy);
            color: var(--tekst-svetlyy);
        }

        &_name {
            flex: 1 0 60%;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }

        &_cost {
            flex: 1 0 16%;
            border-right: 1px solid var(--liniya);
            padding-right: 40px;
        }
        
        &_btn button {
            padding: 8px 16px;
        }
    }
}


// media prices-3
@media (max-width: 950px) {
    .prices {
        &__sort {
            flex-direction: column;
            gap: 12px;
            margin-bottom: 24px;
        }
        &__table {
            &_item {
                width: 100%;
                flex-direction: column;
                align-items: stretch;
                gap: 16px;
            }
            &_name {
                flex: 1 0 100%;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--liniya);
            }
            &_cost {
                flex: 1 0 100%;
                border-right: none;
                padding-right: 0;
            }
        }
    }
}
@media (max-width: 688px) {
    .prices {
        &__table {
            &_item {
                padding: 20px;
            }
            &_name {
                font-size: 14px;
            }
        }
    }
}
@media (max-width: 500px) {
    .prices {
        &__table {
            &_cost span {
                display: block;
            }
            &_btn button {
                width: 100%;
            }
        }
    }
}
