// about-2
.about {
    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__item_between {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__text p:not(:last-child){
        margin-bottom: 20px;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
        overflow: hidden;
    }

    &__advantages {
        display: flex;
        flex-direction: column;
        //gap: 10px;
    }

    &__advantage {
        flex-grow: 1;
        border-radius: 20px;
        padding: 25px;
        background: #414256;
        display: flex;
        flex-direction: column;
        gap: 10px;
        font-size: 14px;
        line-height: 130%;
        color: var(--seryy);

        & p:first-child {
            font-weight: 500;
            & span {
                font-weight: 700;
                font-size: 48px;
                line-height: 100%;
            }
        }

        &:nth-child(2) {
            background: #4b4c60;
            margin-top: -10px;
        }
        &:nth-child(3) {
            background: #55566a;
            margin-top: -10px;
        }
    }
}

// media about-2
@media (max-width: 1050px) {
    .about {
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
        &__img img {
            max-height: 400px;
            object-position: top;
        }
    }
}
@media (max-width: 688px) {
    .about {
        &__text p:not(:last-child){
            margin-bottom: 14px;
        }
        &__img img {
            max-height: 200px;
        }
        &__advantage {
            border-radius: 20px;
            padding: 16px;
            gap: 6px;
            & p:first-child {
                font-size: 12px;
                & span {
                    font-size: 24px;
                }
            }
            &:nth-child(2), &:nth-child(3)  {
                margin-top: -5px;
            }
        }
    }
}